.buttons_container{
    display: grid;
    grid-template-columns:repeat(4,1fr);
    grid-template-rows: repeat(5,60px);
    grid-gap: 10px;
    grid-template-areas: 
    'clear clear percentage eraser'
    'seven eight nine divide'
    'four five six multiply'
    'one two three subtract'
    'zero decimal equals add';
    @import "./button";
}